.about {
  display: flex;
  display: -webkit-flex;
  /* Safari */
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 3rem 0;
  background: #fff;
}
.about .logo {
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
  overflow: hidden;
  background: url(/static/media/home_logo.c2dee6f7.jpg) no-repeat center;
  background-size: cover;
}
.about .info {
  display: flex;
  display: -webkit-flex;
  /* Safari */
  flex-direction: column;
  justify-content: space-around;
  min-height: 8rem;
  width: 30%;
  max-width: 120rem;
  text-align: center;
}
.about .info > p {
  min-width: 50%;
  color: #333;
  font-weight: bold;
  font-size: 1.4rem;
}
