.blog {
  display: flex;
  display: -webkit-flex;
  /* Safari */
  justify-content: center;
  flex-grow: 1;
  width: 90%;
  max-width: 90%;
  align-self: center;
  border-radius: 0.5rem;
}
.blog .article-list {
  display: flex;
  display: -webkit-flex;
  /* Safari */
  justify-content: space-around;
  align-content: space-around;
  flex-wrap: wrap;
  max-width: 120rem;
  padding-top: 4rem;
  background: #fff;
  border-radius: 0.5rem;
}
.blog .article-list .article-item {
  display: flex;
  display: -webkit-flex;
  /* Safari */
  flex-direction: column;
  width: 25rem;
  height: 25rem;
  border-radius: 0.5rem;
  border: 0.1rem solid #ccc;
  padding: 2rem;
  color: #333;
  margin-bottom: 2rem;
  text-decoration: none;
  overflow: hidden;
}
.blog .article-list .article-item .message {
  padding: 1rem 0;
  display: flex;
  display: -webkit-flex;
  /* Safari */
  justify-content: space-around;
}
.blog .article-list .article-item .content {
  text-align: left;
}
.blog .detail {
  border-radius: 0.5rem;
  background: #fff;
  padding: 2rem;
  width: 60rem;
}
.blog .detail .title {
  padding-bottom: 1rem;
}
.blog .detail .btn-edit {
  text-align: right;
}
.blog .detail .btn-edit a {
  display: inline-block;
  color: #fff;
  outline: none;
  border: none;
  background-color: #1890ff;
  height: 2rem;
  line-height: 2rem;
  border-radius: 0.4rem;
  width: 4rem;
  cursor: pointer;
  font-size: 1.2rem;
  text-align: center;
  text-decoration: none;
}
.blog .detail .content {
  padding-top: 1rem;
}
.edit {
  display: flex;
  display: -webkit-flex;
  /* Safari */
  justify-content: center;
  flex-grow: 1;
  background: #fff;
  max-width: 90%;
  align-self: center;
  padding: 1rem 2rem 0;
  border-radius: 0.5rem;
}
.edit .edit-content {
  display: flex;
  display: -webkit-flex;
  /* Safari */
  flex-direction: column;
  background: #fff;
  max-width: 80rem;
  color: #333;
  padding-bottom: 5rem;
}
.edit .edit-content .edit-item {
  display: flex;
  display: -webkit-flex;
  /* Safari */
  flex-direction: column;
  padding-top: 3rem;
}
.edit .edit-content .edit-item > p {
  text-align: left;
  margin-bottom: 1rem;
  font-weight: bold;
}
.edit .edit-content .item-submit {
  align-items: center;
}
.edit .edit-content .title {
  height: 3.2rem;
  line-height: 3.2rem;
  border-radius: 0.4rem;
  outline: none;
  border: 0.1rem solid #d9d9d9;
  text-indent: 1rem;
  font-size: 1.4rem;
}
.edit .edit-content .article-braft-edit {
  border: 0.1rem solid #ccc;
  border-radius: 0.5rem;
}
.edit .edit-content .article-braft-edit .bf-content {
  /* 滚动槽 */
  /* 滚动条滑块 */
}
.edit .edit-content .article-braft-edit .bf-content::-webkit-scrollbar {
  width: 6px;
}
.edit .edit-content .article-braft-edit .bf-content::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(20, 20, 20, 0.3);
  border-radius: 10px;
}
.edit .edit-content .article-braft-edit .bf-content::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: rgba(20, 20, 20, 0.1);
  -webkit-box-shadow: inset 0 0 6px rgba(20, 20, 20, 0.5);
}
.edit .edit-content .submit {
  color: #fff;
  outline: none;
  border: none;
  background-color: #1890ff;
  height: 3.2rem;
  line-height: 3.2rem;
  border-radius: 0.4rem;
  width: 30%;
  cursor: pointer;
  font-size: 1.6rem;
  min-width: 11rem;
}
