* {
  margin: 0;
  padding: 0;
}
html,
body {
  background-color: #282c34;
}
html {
  font-size: 62.5%;
}
body {
  font: normal 100% Arial, sans-serif;
  font-size: 14px;
  font-size: 1.4rem;
}
ul,
li {
  list-style: none;
}
.App {
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  display: -webkit-flex;
  /* Safari */
  flex-direction: column;
  justify-content: space-between;
}
footer {
  height: 8rem;
}
